import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)


const Home = () => import('../views/home/Home.vue')
const Cart = () => import('../views/cart/Cart.vue')
const Catagory = () => import('../views/category/Category.vue')
const Profile = () => import('../views/profile/Profile.vue')
const Detial = () => import('../views/detial/Detial.vue')


const routers = [{
        path: '',
        redirect: '/home'
    },
    {
        name: 'Home',
        path: '/home',
        component: Home
    },
    {
        name: 'Cart',
        path: '/cart',
        component: Cart
    },
    {
        name: 'Catagory',
        path: '/catagory',
        component: Catagory
    },
    {
        name: 'Profile',
        path: '/profile',
        component: Profile
    },
    {
        name: 'Detail',
        path: '/detail/:iid',
        component: Detial
    },
]

const Route = new VueRouter({
    routes: routers,
    mode: 'history'
})

export default Route