<template>
  <div id="tab-bar-item" @click="activeChange">
    <div v-if="isActive">
        <slot name="item-icon"></slot>
    </div>
    <div v-else>
        <slot name="item-icon-active"></slot>
    </div>
    <div :style="activeStyle">
      <slot name="item-text"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "tabbat-item",
  props: {
      path: String,
      activeColor: {
          type: String,
          default: 'blue'
      }
  },
  data() {
    return {
      
    }
  },
  computed: {
    isActive() {
        return this.$route.path.indexOf(this.path) !== -1

    } ,
    activeStyle() {
        return this.isActive?{color: this.activeColor}:{}
    } 
  },
  methods: {
      activeChange() {
          this.$router.replace(this.path)
      }
  }
};
</script>

<style scoped>
#tab-bar-item {
  /* 均等分 */
  flex: 1;
  text-align: center;
  height: 49px;
}


</style>