export default {
    addToCart(context, payload) {
        return new Promise((resolve, reject) => {
            let oldProduct = context.state.cartList.find(item => item.iid === payload.iid)
            if (oldProduct) {
                context.commit('addCount', oldProduct)
                resolve('当前商品数量加1')
            } else {
                context.commit('setCount', payload)
                resolve('添加了新的商品')
            }
        })

    }
}